$bg_color: #051f20;
$title_color: #cbf2c6;
$icon_color: #97bf8d;

ul {
  transform: translate(-50%, -50%);
  display: flex;
}

ul li {
  list-style: none;
  width: 6px;
  height: 20px;
  background: $title_color;
  margin: 0 4px;
  animation: animate 0.5s infinite alternate;
  border-radius: 2px;
}

@keyframes animate {
  0% {
    transform: scaleY(1);
  }
  25% {
    transform: scaleY(1);
  }
  50% {
    transform: scaleY(1);
  }
  75% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(3);
  }
}

ul li:nth-child(1) {
  animation-delay: 0.1s;
}

ul li:nth-child(2) {
  animation-delay: 0.2s;
}

ul li:nth-child(3) {
  animation-delay: 0.3s;
}

ul li:nth-child(4) {
  animation-delay: 0.4s;
}

ul li:nth-child(5) {
  animation-delay: 0.5s;
}

ul li:nth-child(6) {
  animation-delay: 0.6s;
}
